import { Link } from "react-router-dom";

function Footer() {
  return (
    <>
      <div className=" bg-gray-900">
        <div className="max-w-2xl mx-auto text-white py-10">
        <footer className=" text-white text-center p-4">
        <div className="mb-2">
          <Link to="#" className="text-white hover:underline mx-2">Privacy Policy</Link>
          <Link to="#" className="text-white hover:underline mx-2">Terms of Service</Link>
          <Link to="#" className="text-white hover:underline mx-2">Contact Us</Link>
        </div>
        <div className="mb-2">
          <Link to="#"><img src="facebook-icon.png" alt="Facebook" className="inline-block h-6 w-6 mx-1" /></Link>
          <Link to="#"><img src="twitter-icon.png" alt="Twitter" className="inline-block h-6 w-6 mx-1" /></Link>
          <Link to="#"><img src="linkedinicon.png" alt="LinkedIn" className="inline-block h-6 w-6 mx-1" /></Link>
        </div>
        <p>&copy; 2024 Career Craft. All rights reserved.</p>
      </footer>
        </div>
      </div>
    </>
  );
}

export default Footer;
